var state = {
    db : db,
    testimonials : [],
    filteredTestimonials : [],
    categories : [],
    filteredCategories : [],
    cardsStack : null,
    cardsElement : null,
    dialogElem : null,
    isCardsLoading : true
}

var mutations = {
    setFilters : (state, data) => {
        state.filteredCategories = data;

        if(state.filteredCategories.length > 0){
            var ft = []
            state.testimonials.filter( testimonial =>{
                if(state.filteredCategories.includes(testimonial.category))
                    ft.push(testimonial)
            })
            state.filteredTestimonials = ft
            //console.log("Filtered" + state.filteredTestimonials)
        } else{
            state.filteredTestimonials = state.testimonials
            //console.log("Constant" + state.filteredTestimonials)
        }
            
    },
    setCategories : (state, data) => state.categories = data,
    setDialog : (state, data) => state.dialogElem = data,
    setTestimonials : (state, data) => {
        state.testimonials = data,
        state.filteredTestimonials = data
    },
    setCardsStack : (state, data) =>{
        state.cardsElement = data,
        state.cardsStack = new Stack(state.cardsElement)
        //console.log(state.cardsElement)
        //console.log(state.cardsStack)
    },
    shuffelTestimonials : (state,data) => {
        if(data){
            var testimonials = state.filteredTestimonials
            var shuffle = function(a) {
                var j, x, i;
                for (i = a.length; i; i--) {
                    j = Math.floor(Math.random() * i);
                    x = a[i - 1];
                    a[i - 1] = a[j];
                    a[j] = x;
                }
            }
            shuffle(testimonials)
            state.filteredTestimonials = testimonials
            state.cardsStack = new Stack(data)
        }
        
        
    },
    updateTData : (state,data) => {
        //console.log(data)
        let ut = []
        state.filteredTestimonials.map( testimonial => {
            if(testimonial.id == data.id){
                testimonial.client = data.client
                testimonial.testimonial = data.testimonial
            }
            ut.push(testimonial)
        });
        state.filteredTestimonials = ut
    }
}

var actions = {
    AddFilters : (context, data) => context.commit("setFilters", data),
    AddCategories : (context, data) => context.commit("setCategories", data),
    AddTestimonials : (context, data) => context.commit("setTestimonials", data),
    AddCardsStack : (context, data) => context.commit("setCardsStack", data),
    AddDialog : (context, data) => context.commit("setDialog", data),
    ShuffelCards : (context,data) => context.commit("shuffelTestimonials",data),
    UpdateTestimonial : (context,data) => context.commit("updateTData",data)
}

var getters = {
    getCategories : state => state.categories,
    getFilters: state => state.filteredCategories,
    getTestimonials: state => state.testimonials
}

export const store = new Vuex.Store({
    state : state,
    mutations : mutations,
    getters : getters,
    actions : actions
})


