<template>
  <div id="editDialog" class="dialog">
    <div class="dialog__overlay"></div>
    <div class="dialog__content">
      <button class="close" data-dialog-close>X</button>
      <div class="image">
        <span class="qt">“</span>
        <h3>Where do new ideas come from? The answer is simple: differences. Creativity comes from unlikely juxtapositions.</h3>
        <p>Nicholas Negroponte</p>
      </div>
      <form class="form" v-on:submit.prevent="saveData">
        <input type="hidden" name="docid" id="docid">
        <input type="hidden" name="cat" id="cat">
        <div class="group">
          <h3>Customize</h3>	
        </div>
        <div class="input group">
          <label class="name">
            <input type="text" name="name" id="name" placeholder="Your Name" required>
          </label>
        </div>
        <div class="input group">
          <label class="company">
            <input type="text" name="company" id="company" placeholder="Company">
          </label>
        </div>
        <div class="input group">
          <label class="email">
            <input type="text" name="email" id="email" placeholder="Email" required>
          </label>
        </div>
        <div class="input group">
          <label class="quote">
            <textarea name="testimonial" id="testimonial" placeholder="Enter your testimonial" rows="5" required></textarea>
          </label>
        </div>
        <button class="save action">Save</button>
        
      </form>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Customize',
  created(){

  },
  updated(){
    //this.saveData();
  },
  methods: {
    saveData(e){
      let self = this;
      let elems = e.target.elements;
      let name = elems.name.value;
      let company = elems.company.value;
      let email = elems.email.value;
      let testimonial = elems.testimonial.value;
      let id = elems.docid.value;
      let category = elems.cat.value;

      this.$store.state.db.collection("tg-testimonial").doc(id).set({
          category : category,
          client : name,
          testimonial: testimonial
      }).then(function() {
          //alert("Document successfully written!");
            self.$toasted.show("Testimonial Updated Successfully!!", { 
                theme: "primary", 
                icon : 'error_outline',
                position: "top-center", 
                duration : 2000,
                dontClose : true
            })
            let d =  {
              id : id, 
              testimonial : testimonial, 
              client : name
            }
            //console.log(d)
            self.$store.dispatch("UpdateTestimonial",d)
            self.$store.state.dialogElem.toggle()
      })
      .catch(function(error) {
          //alert("Error writing document: ", error);
            self.$toasted.show("Oops Something went wrong!!", { 
                theme: "primary", 
                icon : 'error_outline',
                position: "top-center", 
                duration : 2000,
                dontClose : true
            });
            self.$store.state.dialogElem.toggle()
            //console.log(error)
      });
      this.$store.state.db.collection("tg-users").add({
          name: name,
          company: company,
          email : email
      })
      .then(function(docRef) {
          //alert("Document written");
      })
      .catch(function(error) {
          //alert("Error adding document: ", error);
      });
    } 
  }
}
</script>


