<template>
    <div class="cats group" id="category">
        <ul class="category ac-custom ac-checkbox ac-checkmark">
            <li class="subtitle">
                <h3>Categories</h3>
                <span v-if="stillLoading" class="loader"></span>
                <span v-else class="filter"></span>
            </li>
            <li v-for="(category, index) in $store.state.categories" :key="`category-${index}`" v-if="index < 3">
                <input type="checkbox" v-model="selectedCategories" :id="category.id" :value="category.name" :checked="false" @change="updateCats">
                <label :for="category.id">{{category.name}}</label>
            </li>
        </ul>
        <ul class="category ac-custom ac-checkbox ac-checkmark">
            <li v-for="(category, index) in $store.state.categories" :key="`category-${index}`" v-if="index > 2">
                <input type="checkbox" v-model="selectedCategories" :id="category.id" :value="category.name" :checked="false" @change="updateCats">
                <label :for="category.id">{{category.name}}</label>
            </li>
        </ul>

    </div>
</template>

<script>

export default {
    name : 'Categories',
    data () {
        return {
            stillLoading : true,
            selectedCategories : []
        }
    },
    created(){
        //console.log(this.$store.state.categories)
    },
    updated(){
        this.stillLoading = false
    },
    methods: {
        updateCats(){
            this.$store.dispatch("AddFilters", this.selectedCategories)
            this.$store.dispatch("ShuffelCards")
            let el = document.querySelector( ':focus' );
            if( el ) el.blur();
        }
    }
    
}
</script>



