<template>
  <div class="cards group" id="quotesBlock">
    <div class="loader stack__item" v-bind:class="{ active: isLoading }">
      <div class="quote">
        <span class="qt">“</span>
        <h3>Please wait. Your Testimonial are being generated !!</h3>
      </div>
      <span class="author">Testimonial Generator</span>
    </div>
    <ul id="stack_yuda" class="stack stack--yuda" ref="cardsStack">
        <li class="stack__item" v-for="(card, index) in $store.state.filteredTestimonials" :key="`card-${index}`">
            <span class="copy-btn" v-clipboard="card.testimonial + ' - ' + card.client" v-clipboard:success="OnCopy" v-clipboard:error="OnError">copy</span>
            <div class="quote" :id="card.category"><span class="qt">“</span><h3>{{card.testimonial}}</h3></div>
            <span class="author">{{card.client}}</span>
            <span class="copy-btn customize" :id="card.id" @click="EditTestimonial($event)">Customize</span>
		</li>
    </ul>
    <div class="controls">
      <button class="button button--sonar button--reject button--default" data-stack="stack_yuda" @click="changeCard">Next</button>
      <h3>or press <span class="highlight">spacebar</span></h3>
    </div>
  </div>
</template>

<script>
import '../assets/js/classie.js'
import '../assets/js/dynamics.min.js'
import '../assets/js/main.js'

export default {
    name : "Testimonials",
    data () {
        return {
          isLoading : true,
          customizeData : []
        }
    },
    created(){
        this.isLoading = true
    },
    updated(){
        this.isLoading = false
        this.init()
    },
    mounted(){
        if(!this.isLoading){
            this.$store.dispatch("ShuffelCards",this.$refs["cardsStack"])
            //console.log(this);
            this.isLoading = false
        }
        
    },
    methods : {
        init(){
            const self = this
            this.isLoading = false

            self.$store.dispatch("AddCardsStack", this.$refs["cardsStack"])
            window.onkeydown = function(e) {
                if(e.keyCode == 32 && e.target == document.body) {
                    e.preventDefault();
                    self.$store.state.cardsStack.reject(); 
                    return false;
                }
            }
        },
        EditTestimonial(e){
            let self = this;
            let editDialog = document.getElementById("editDialog"),
            dlg = new DialogFx( editDialog );
            self.$store.dispatch("AddDialog", dlg)

            self.customizeData[0] = e.target.id;
            self.customizeData[1] = e.target.previousElementSibling.innerText;
            self.customizeData[2] = e.target.previousElementSibling.previousElementSibling.querySelector("h3").innerText;
            self.customizeData[3] = e.target.previousElementSibling.previousElementSibling.id;

            editDialog.querySelector(".image h3").innerText = self.customizeData[2];
            editDialog.querySelector(".image p").innerText = self.customizeData[1];
            editDialog.querySelector("[name='docid']").value = self.customizeData[0];
            editDialog.querySelector("[name='cat']").value = self.customizeData[3];
            editDialog.querySelector("[name='testimonial']").value = self.customizeData[2];

            this.$store.state.dialogElem.toggle()
        },
        changeCard(){
            this.$store.state.cardsStack.accept()
        },
        OnCopy ({ value, event }) {
            //console.log(event.target.innerText)
            event.target.innerText = "copied!"
            setTimeout(()=> {
                event.target.innerText = "copy";
            },2000)
        },
        OnError ({ value, event }) {
            //console.log('error', value)
        }
    }
}
</script>

<style>

</style>



