<template>
  <div id="app">
  <div class="main group">

    <div class="left group">

      <div class="logo group">
        <router-link to="/"><img src="./assets/img/logo.png" alt="Testimonial Generator"></router-link>
      </div>

      <h2 class="title group">
        <span class="highlight">Amazing Testimonial</span> for your business or profession
      </h2>

      <Categories />

    </div>

    <div class="right group">
      <transition name="fadeIn">
        <router-view></router-view>
      </transition>	
    </div>
  </div>
  <Footer />
  <Customize />
  </div>
</template>

<script>
import Categories from "./components/Categories"
import Footer from "./components/Footer"
import Customize from "./components/Customize"

export default {
  name: 'App',
  components:{
    Categories,
    Footer,
    Customize
  },
  created(){
    //console.log(this.$store.state.db)
    var AppData = [], CategoriesData = []

    this.$store.state.db.collection('tg-testimonial').get().then(data =>{
        const Cats = []
        data.forEach(testimonial => {
            const data = {
                'id' : testimonial.id,
                'category' : testimonial.data().category,
                'testimonial': testimonial.data().testimonial,
                'client' : testimonial.data().client
            },
            category = {
              'id' : testimonial.id,
              'name' : testimonial.data().category.toLowerCase()
            }
            AppData.push(data)
            Cats.push(category)
        });

        function removeDuplicates(myArr, prop) {
            return myArr.filter((obj, pos, arr) => {
            return arr.map(mapObj =>
            mapObj[prop]).indexOf(obj[prop]) === pos;
        })}

        this.$store.dispatch("AddTestimonials", AppData)

        CategoriesData = removeDuplicates(Cats,"name")

        this.$store.dispatch("AddCategories", CategoriesData)    
        //console.log(CategoriesData)

    });
  }
}
</script>

<style>

</style>



